export default {
  data () {
    return {
      content:'',
      title:'',
      img:''
    }
  },
  methods:{
    getInfo() {
      let id = this.$route.query.id
      this.axios.get('zhihu/api/4/news/'+id).then(resp => {
           console.log(resp)
          this.title = resp.data.title
          this.img = resp.data.image
          this.content = resp.data.body
      }).catch(err => {
          console.log(err);
      })
    }
  },
  mounted() {
     this.getInfo()
  }    
}



