
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import showdoc from '../assets/showdoc.png'
import mini from '../assets/mini.png'
import backstage from '../assets/backstage.png'
import official from '../assets/official.png'
export default {
name: 'Home',
data() {
return {
news: '',
news_list: [],
demo01_list: [{
url: 'javascript:',
img: showdoc,
title: 'showdoc'
}, {
url: 'javascript:',
img: backstage,
title: ''
}, {
url: 'javascript:',
img: mini,
title: '',
}, {
url: 'javascript:',
img: official,
title: '',
}]

}
},
components: {
Swiper,
SwiperItem,
Group,
Cell,
CellBox,
Badge,
Search,
Grid,
GridItem
},
methods: {
getNews() {
this.axios.get('/zhihu/api/4/news/latest').then(resp => {
console.log(resp.data.top_stories)
this.news = ' ' + resp.data.date
this.news_list = resp.data.top_stories
}).catch(err => {
console.log(err);
})
}
},
created() {
this.getNews()
}
}



