
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'


export default {
components: {
XInput,
Group,
XTextarea,
XButton,
Box
},
data () {
return {
submit:'',
disabled:false,
title:'',
content:'',
realname:'',
mobile:''
}
},
methods: {
handleSubmit() {
if(this.title==''){
this.$vux.toast.show({
text:'',
type:'cancel',
position:'middle'
})
return false
}
if(this.content==''){
this.$vux.toast.show({
text:'',
type:'cancel',
position:'middle'
})
return false
}
if(this.realname==''){
this.$vux.toast.show({
text:'',
type:'cancel',
position:'middle'
})
return false
}
if(!(/^1[34578]\d{9}$/.test(this.mobile))){
this.$vux.toast.show({
text:'',
type:'cancel',
position:'middle'
})
return false
}

}
}
}



